<template>
    <div>
        <h3>插槽</h3>
        <!-- 默认插槽 -->
         <!-- <child-component>你好</child-component> -->
         <!-- 具名插槽 -->
         <!-- <child-component>
        <template slot="girl">
            漂亮、美丽、购物、逛街
        </template>
        <template slot="boy">
            帅气、才实
        </template>
        <div>
            我是一类人，
            我是默认的插槽
        </div>
    </child-component> -->
    <!-- 作用域插槽 -->
     <child :lists="nameList">
        <template slot-scope="a">
            <div v-if='a.bbbbb.id==1'>你好：<span>{{a.bbbbb.name}}</span></div>
            <div v-else>{{a.bbbbb.name}}</div>
        </template>
    </child>
    </div>
</template>
<script>
import Vue from 'vue'
// 插槽当插入的元素没有slot标签的话 原来元素里的内容会不显示    必须加上slot标签原来的内容和插入的内容才会都显示
//  Vue.component('child-component',{
//         template:`
        
//             <div>Hello,World! <slot></slot></div>
//         `
//     })
// Vue.component('child-component',{
//         template:`
//             <div>
//             <h4>这个世界不仅有男人和女人</h4>
//             <slot name="girl"></slot>
//             <div style="height:1px;background-color:red;"></div>
//             <slot name="boy"></slot>
//             <div style="height:1px;background-color:red;"></div>
//             <slot></slot>
//             </div>
//         `
//     })
Vue.component('child',{
        props:['lists'],
        template:`
            <div>
                <ul>
                    <li v-for="list in lists">
                        <slot :bbbbb="list"></slot>
                    </li>
                </ul>
            </div>
        `
    })
export default {
    data(){
        return{
            nameList:[
            {id:1,name:'孙悟空'},
            {id:2,name:'猪八戒'},
            {id:3,name:'沙和尚'},
            {id:4,name:'唐僧'},
            {id:5,name:'小白龙'},
            ]
        }
    }
}
</script>



